<template>
	<transition name="toRight">
		<div class="content">
			<div class="content-wrapper" v-if="bookLoader">
				<div class="content-item" v-for="(item,index) in navigation.toc" @click="jump(item.href)" :key="index">
					<span class="text">{{item.label}}</span>
				</div>
			</div>
			<div class="empty" v-else>加载中...</div>
		</div>
	</transition>
</template>

<script>
export default{
	props:{
		ifShowContent:Boolean,
		navigation:Object,
		bookLoader:Boolean,
	},
	methods:{
		jump(href){
			this.$emit('jump',href);
		}
	}
}
</script>

<style lang="scss" scoped>
@import '../assets/style/global';
.content{
	position: absolute;
	top:0;
	left:0;
	font-size: px2rem(14);
	overflow: hidden;
	overflow-y: scroll;
	height: 100%;
	width: 80%;
	z-index: 102;
	background: #fff;
	.content-wrapper{
		padding: px2rem(20);
		.content-item{
			margin-bottom: px2rem(5);
		}
	}
	.empty{
		height:100vh;
		overflow: hidden;
		@include center;
	}
}
</style>