<template>
	<div class="bar">
		<transition name='toUp'>
			<div class="menu-wrapper" v-show="isShow" :class="{'hide-box-shadow':issetShow || !isShow}">
				<div class="icon-wrapper" @click="settingShow(1)">
					<span class="icon icon-caidan"></span>
				</div>
				<div class="icon-wrapper" @click="settingShow(2)">
					<span class="icon icon-shezhi"></span>
				</div>
				<div class="icon-wrapper">
					<span class="icon icon-liangdu" @click="settingShow(3)"></span>
				</div>
				<div class="icon-wrapper">
					<span class="icon" @click="settingShow(4)">A</span>
				</div>
			</div>
		</transition>
		<transition name='toUp'>
			<div class="set-wrapper" v-show="issetShow">
				<div class="setfont" v-if="showType==4">
					<div class="left" :style="{fontSize:fontSizeList[0].fontSize+'px'}">A</div>
					<div class="progress_box">
						<div class="progress" @click="setFontSize(item.fontSize)" v-for="(item,index) in fontSizeList" :key="index">
							<div class="line"></div>
							<div class="point">
								<div class="stor" v-show="item.fontSize===defaultSize">
									<div class="small_stor"></div>
								</div>
							</div>
							<div class="line"></div>
						</div>
					</div>
					<div class="right" :style="{fontSize:fontSizeList[fontSizeList.length-1].fontSize+'px'}">A</div>
				</div>
				<div class="settheme" v-else-if="showType==3">
					<div class="theme-box" v-for="(item,index) in themesList" :key="index" @click="setThemes(index)">
						<div class="color-box" :style="{background:item.style.body.background}" :class="{'no-border':item.style.body.background!=='#fff'}"></div>
						<div class="txt-box" :class="{'selected':index===defaultTheme}">{{item.name}}</div>
					</div>
				</div>
				<div class="setprogress" v-else-if="showType==2">
					<div class="progress-box">
						<input class="progress" type="range" max="100" min="0" step="1" :value="progress" :disabled="!bookLoader" @change="progressChange($event.target.value)" @input="progressInput($event.target.value)" ref="progress" />
					</div>
					<div class="txt-box">
						<span>{{bookLoader ? progress + '%' :'加载中...'}}</span>
					</div>
				</div>
			</div>
		</transition>
		<content-view
			:ifShowContent="ifShowContent"
			v-show="ifShowContent"
			:navigation="navigation"
			:bookLoader="bookLoader"
			@jump="jump"></content-view>
		<transition name="fade">
			<div class="content-mask"
				v-show="ifShowContent"
				@click="hideContent()"></div>
		</transition>
	</div>
</template>

<script>
	import ContentView from '@/components/Content'
	export default {
		components:{
			ContentView
		},
		props: {
			isShow: {
				type: Boolean,
				default: false
			},
			fontSizeList: Array,
			defaultSize: Number,
			themesList: Array,
			defaultTheme: Number,
			bookLoader: {
				type: Boolean,
				default: false
			},
			navigation:Object
		},
		data() {
			return {
				issetShow: false,
				showType: 4,
				progress: 0,
				ifShowContent:false
			}
		},
		methods: {
			//设置条是否显示
			settingShow(index) {
				//this.issetShow = !this.issetShow;
				this.showType = index;
				if(this.showType==1){
					this.ifShowContent = true;
					this.issetShow = false;
				}else{
					this.issetShow = true;
				}
			},
			//隐藏设置条 提供给其他组件使用
			hideSet() {
				this.issetShow = false;
			},
			//设置字体大小-通过父组件
			setFontSize(fontSize) {
				this.$emit('setFontSize', fontSize);
			},
			//设置主题--通过父组件
			setThemes(index) {
				this.$emit('setThemes', index);
			},
			//改变进度条
			progressInput(progress) {
				this.progress = progress;
				this.$refs.progress.style.backgroundSize = "" + this.progress + "% 100%";
			},
			//进度条改变加载对应数据--通过父组件
			progressChange(progress) {
				this.$emit('progressChange', progress)
			},
			//隐藏目录
			hideContent(){
				this.ifShowContent = false;
			},
			//根据目录链接跳转
			jump(href){
				this.hideContent();
				this.$emit('jump',href);
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import '../assets/style/global';
	.bar {
		.menu-wrapper {
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: px2rem(48);
			background: #fff;
			box-shadow: 0 px2rem(-8) px2rem(8) rgba(0, 0, 0, .2);
			z-index: 101;
			display: flex;
			.icon-wrapper {
				flex: 1;
				@include center;
			}
			/*设置条展开隐藏菜单栏的阴影*/
			&.hide-box-shadow {
				box-shadow: none;
			}
		}
		.set-wrapper {
			position: absolute;
			bottom: px2rem(48);
			left: 0;
			width: 100%;
			height: px2rem(60);
			background: #fff;
			box-shadow: 0 px2rem(-8) px2rem(8) rgba(0, 0, 0, .2);
			z-index: 101;
			.setfont {
				height: 100%;
				display: flex;
				.left {
					/*flex: 0 0 px2rem(40);*/
					/*@include center;*/
					width: calc(100% / 9);
					display: flex;
					align-items: center;
					justify-content: flex-end;
					padding-right: calc(100% / 36);
				}
				.right {
					/*flex: 0 0 px2rem(40);*/
					/*@include center;*/
					width: calc(100% / 9);
					display: flex;
					align-items: center;
					justify-content: flex-start;
					padding-left: calc(100% / 36);
				}
				.progress_box {
					flex: 1;
					@include center;
					.progress {
						height: 100%;
						flex: 1;
						display: flex;
						align-items: center;
						&:first-child {
							.line {
								&:first-child {
									border-top: none;
								}
							}
						}
						&:last-child {
							.line {
								&:last-child {
									border-top: none;
								}
							}
						}
						.line {
							flex: 1;
							height: 0;
							border-top: 1px solid #c1c1c1;
						}
						.point {
							position: relative;
							height: 7px;
							flex: 0 0 0;
							border-left: 1px solid #c1c1c1;
							.stor {
								position: absolute;
								top: px2rem(-8);
								left: px2rem(-10);
								width: px2rem(20);
								height: px2rem(20);
								border-radius: 50%;
								border: px2rem(1) solid #c1c1c1;
								box-shadow: 0 px2rem(4) px2rem(4) rgba(0, 0, 0, .2);
								background: #fff;
								@include center;
								.small_stor {
									background: #000;
									width: px2rem(4);
									height: px2rem(4);
									border-radius: 50%;
								}
							}
						}
					}
				}
			}
		}
		.settheme {
			display: flex;
			height: 100%;
			.theme-box {
				flex: 1;
				display: flex;
				flex-direction: column;
				box-sizing: border-box;
				padding: px2rem(5);
				.color-box {
					flex: 1;
					border: px2rem(1) solid #ccc;
					box-sizing: border-box;
					&.no-border {
						border: none;
					}
				}
				.txt-box {
					flex: 1;
					font-size: px2rem(14);
					text-align: center;
					color: #ccc;
					@include center;
					&.selected {
						color: #333;
					}
				}
			}
		}
		.setprogress {
			position: relative;
			height: 100%;
			width: 100%;
			.progress-box {
				height: 100%;
				width: 100%;
				padding: 0 px2rem(30);
				box-sizing: border-box;
				@include center;
				.progress {
					width: 100%;
					-webkit-appearance: none;
					height: px2rem(2);
					background: -webkit-linear-gradient(#999, #999) no-repeat, #ddd;
					background-size: 0 100%;
					&:focus {
						outline: none;
					}
					&::-webkit-slider-thumb {
						-webkit-appearance: none;
						height: px2rem(20);
						width: px2rem(20);
						border-radius: 50%;
						background: #fff;
						box-shadow: 0 px2rem(4) px2rem(4) rgba(0, 0, 0, .2);
						border: px2rem(1) solid #ddd;
					}
				}
			}
			.txt-box {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				text-align: center;
				color: #333;
				font-size: px2rem(12);
			}
		}
		.content-mask{
			position: absolute;
			top: 0;
			left:0;
			width: 100%;
			height: 100%;
			z-index: 101;
			display: flex;
			background: rgba(51,51,51,.8);
		}
	}
</style>