<template>
	<div class="ebook">
		<!--<title-bar :isShow="isShow"></title-bar>-->
		<div class="read-wrapper">
			<div id="read"></div>
			<div class="mask">
				<div class="left" @click="prePage()"></div>
				<div class="center" @click="toggleShow()"></div>
				<div class="right" @click="nextPage()"></div>
			</div>
		</div>
		<menu-bar :isShow="isShow" 
			ref="menuBar" 
			:bookLoader="bookLoader" 
			@setFontSize="setFontSize" 
			:defaultSize="defaultSize" 
			:defaultTheme="defaultTheme" 
			:themesList="themesList" 
			@setThemes="setThemes" 
			@progressChange="progressChange"
			@jump="jump"
			:navigation="navigation"
			:fontSizeList="fontSizeList"></menu-bar>
	</div>
</template>

<script>
	import Epub from 'epubjs'
	const epubUrl = 'static/xiaoshuo.epub'
	global.ePub = Epub;

	import TitleBar from '@/components/TitleBar'
	import MenuBar from '@/components/MenuBar'
	export default {
		components: {
			TitleBar,
			MenuBar
		},
		data() {
			return {
				isShow: false,
				fontSizeList: [{
						fontSize: 12
					},
					{
						fontSize: 14
					},
					{
						fontSize: 16
					},
					{
						fontSize: 18
					},
					{
						fontSize: 20
					},
					{
						fontSize: 22
					},
					{
						fontSize: 24
					},
				],
				defaultSize: 16,
				defaultTheme: 0,
				themesList: [{
						name: 'default',
						style: {
							body: {
								'color': '#000',
								'background': '#fff'
							}
						}
					},
					{
						name: 'eye',
						style: {
							body: {
								'color': '#000',
								'background': '#ceeaba'
							}
						}
					},
					{
						name: 'night',
						style: {
							body: {
								'color': '#fff',
								'background': '#000'
							}
						}
					},
					{
						name: 'gold',
						style: {
							body: {
								'color': '#000',
								'background': 'rgb(241,236,226)'
							}
						}
					}
				],
				//小说是否加载完毕
				bookLoader:false,
				navigation:{}
			}
		},
		methods: {
			//生成电子书以及渲染
			showEpub() {
				//生成book对象
				this.book = new Epub(epubUrl);
				//生成rendition 对象  挂载在某个节点
				this.rendition = this.book.renderTo('read', {
					width: window.innerWidth,
					height: window.innerHeight
				})
				//通过rendtion.display()渲染电子书
				this.rendition.display();
				//获取当前主题对象
				this.themes = this.rendition.themes;
				//设置默认字体
				this.setFontSize(this.defaultSize);
				//注册主题对象  this.themes.register();
				this.regThemes();
				//使用主题  this.themes.select()
				this.setThemes(this.defaultTheme);
				//获取location对象
				//通过epubjs的钩子函数  生成的是promise对象
				this.book.ready.then(()=>{
					this.navigation = this.book.navigation;  //获取标题目录以及跳转链接
					return this.book.locations.generate();
				}).then(result=>{
					this.locations = this.book.locations;
					this.bookLoader = true;
				})
			},
			//上一页
			prePage() {
				if(this.rendition) {
					this.rendition.prev()
				}
			},
			//下一页
			nextPage() {
				if(this.rendition) {
					this.rendition.next();
				}
			},
			//是否显示菜单和标题
			toggleShow() {
				this.isShow = !this.isShow;
				if(!this.isShow) {
					//通过ref调用子组件的方法
					this.$refs.menuBar.hideSet();
				}
			},
			//设置字体大小
			setFontSize(fontSize) {
				this.defaultSize = fontSize;
				if(this.themes) {
					this.themes.fontSize(fontSize + 'px');
				}
			},
			//注册主题
			regThemes() {
				this.themesList.forEach((theme) => {
					this.themes.register(theme.name, theme.style);
				})
			},
			//设置主题
			setThemes(index) {
				this.themes.select(this.themesList[index].name);
				this.defaultTheme = index;
			},
			//进度条 progress 
			progressChange(progress) {
				const percentage = progress / 100;
				let page = percentage > 0 ? this.locations.cfiFromPercentage(percentage) : 0;
				this.rendition.display(page);
			},
			//目录跳转
			jump(href){
				this.rendition.display(href);
				this.hideTitleMenu();
			},
			//隐藏标题和菜单栏
			hideTitleMenu(){
				this.isShow = false;
				this.$ref.menuBar.hideSet();
				this.$ref.menuBar.hideContent();
			}
		},
		mounted() {
			this.showEpub();
		}
	}
</script>

<style lang="scss" scoped>
	@import 'assets/style/global';
	.ebook {
		position: relative;
		.read-wrapper {
			.mask {
				position: absolute;
				top: 0;
				left: 0;
				z-index: 100;
				width: 100%;
				height: 100%;
				display: flex;
				.left {
					flex: 0 0 px2rem(100);
				}
				.center {
					flex: 1;
				}
				.right {
					flex: 0 0 px2rem(100);
				}
			}
		}
	}
</style>